import axios from "axios";
import React, { Component } from "react";
import { withRouter } from "react-router";
import s from "./about-book.module.css";
import defaultCover from "../../images/bookCoverDefault.jpg";
import avatar from "../../images/avatar.jpg"
import { NavLink } from "react-router-dom";

class AboutBook extends Component {
  state = {
    cover: null,
    author: null,
    description: "",
    title: "",
    id: null,
    authorPhotoId: null,
    authorName: '',
  };

  componentDidMount() {
    axios
      .get(
        `https://openlibrary.org/works/OL${this.props.match.params.bookId}W.json`
      )
      .then((response) => response.data)
      .then((data) => {
        this.setState({
          title: data.title,
          description: data.description,
          id: this.props.match.params.bookId,
          author: data.authors[0].author.key,
          cover: data?.covers && data.covers[0],
        });
        axios
          .get(`https://openlibrary.org${this.state.author}.json`)
          .then((response) => response.data)
          .then((data) =>
            this.setState({
              authorPhotoId: data?.photos ? data.photos[0] : null,
              authorName: data.name,
            })
          );
      });
  }

  render() {
    const splitedName = this.state.authorName.split(' ')
    const searchName = splitedName[splitedName.length - 1]

    return (
      <div className={s.content + " container-lg d-flex flex-wrap " + (this.props.darkMode && s.dark)}>
        <div className="col-12 col-md-4 mt-3">
          <img
            src={
              this.state.cover
                ? `https://covers.openlibrary.org/b/id/${this.state.cover}-L.jpg`
                : defaultCover
            }
            alt="Sorry"
            className={s.coverPhoto + " img-fluid"}
          />
          <div className="mt-3 mb-2">
            <a
              href={`https://openlibrary.org/works/OL${this.props.match.params.bookId}W`}
              target="new"
              className='text-decoration-none fs-5'
            >
              Original Site
            </a>
          </div>
        </div>

        <div className="col-12 col-md-6 offset-md-1">
          <div className="fs-4 mt-3 mb-1">{this.state.title}</div>
          <div>{
              !this.state.description
              ? "Sorry, We do not have description for this book. For more information about the book click on the link belov cover image"
              : (!this.state.description.value
              ? (this.state.description.indexOf('http') === -1 ? this.state.description : this.state.description.slice(0,this.state.description.indexOf('http') - 1))
              : (this.state.description.value.indexOf('http') === -1 ? this.state.description.value : this.state.description.value.slice(0,this.state.description.value.indexOf('http') - 1)))
            }</div>

          <div className="mt-4">
            <div>
              
                <img
                className={s.authorPhoto}
                src={this.state.authorPhotoId ? ` https://covers.openlibrary.org/b/id/${this.state.authorPhotoId}-M.jpg` : avatar}
                alt="Sorry"
              />
              
              
            </div>
            <div className="fs-5 mt-1 mb-2">Author:
              <NavLink to={`/authors/${searchName}/1`}>{' ' + this.state.authorName}</NavLink>
             </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(AboutBook);
